"use client"
import React, { useState } from 'react';
import { Calendar, MapPin, Users, Plane, Search, Sparkles } from 'lucide-react';

const TourCard = () => {
    const [departureAirport, setDepartureAirport] = useState('London');
    const [packageType, setPackageType] = useState('Umrah');
    const [travelMonth, setTravelMonth] = useState('');
    const [pilgrims, setPilgrims] = useState('01');

    return (
        <div className="relative w-full bg-gradient-to-br from-sky-50 via-blue-50 to-cyan-50 rounded-3xl shadow-2xl p-8 overflow-hidden">
            {/* Decorative background elements */}
            <div className="absolute top-0 right-0 w-64 h-64 bg-sky-200/30 rounded-full blur-3xl"></div>
            <div className="absolute bottom-0 left-0 w-64 h-64 bg-cyan-200/30 rounded-full blur-3xl"></div>
            
            <div className="relative z-10">
                {/* Header */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center gap-2 text-white px-6 py-2 rounded-full mb-4 shadow-lg" style={{background: '#28AAE2'}}>
                        <Sparkles className="w-4 h-4" />
                        <span className="font-semibold">Plan Your Sacred Journey</span>
                    </div>
                    <h2 className="text-3xl font-bold text-gray-800 mb-2">Find Your Perfect Package</h2>
                    <p className="text-gray-600">Select your preferences and discover tailored Umrah packages</p>
                </div>

                {/* Form Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
                    {/* Departure Airport */}
                    <div className="group">
                        <label className="flex items-center gap-2 text-sm font-semibold text-gray-700 mb-3">
                            <Plane className="w-4 h-4" style={{color: '#28AAE2'}} />
                            Departure Airport
                        </label>
                        <div className="relative">
                            <select 
                                value={departureAirport}
                                onChange={(e) => setDepartureAirport(e.target.value)}
                                className="w-full px-4 py-4 bg-white border-2 border-gray-200 rounded-2xl shadow-sm transition-all duration-300 appearance-none cursor-pointer text-gray-800 font-medium hover:shadow-md"
                                style={{borderColor: 'rgb(229, 231, 235)'}}
                                onFocus={(e) => {
                                    e.target.style.borderColor = '#28AAE2';
                                    e.target.style.boxShadow = '0 0 0 4px rgba(40, 170, 226, 0.1)';
                                }}
                                onBlur={(e) => {
                                    e.target.style.borderColor = 'rgb(229, 231, 235)';
                                    e.target.style.boxShadow = '';
                                }}
                            >
                                <option>London</option>
                                <option>Manchester</option>
                                <option>Birmingham</option>
                                <option>Glasgow</option>
                            </select>
                            <MapPin className="absolute right-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 pointer-events-none" />
                        </div>
                    </div>

                    {/* Package Type */}
                    <div className="group">
                        <label className="flex items-center gap-2 text-sm font-semibold text-gray-700 mb-3">
                            <Sparkles className="w-4 h-4" style={{color: '#28AAE2'}} />
                            Package Type
                        </label>
                        <div className="relative">
                            <select 
                                value={packageType}
                                onChange={(e) => setPackageType(e.target.value)}
                                className="w-full px-4 py-4 bg-white border-2 border-gray-200 rounded-2xl shadow-sm transition-all duration-300 appearance-none cursor-pointer text-gray-800 font-medium hover:shadow-md"
                                onFocus={(e) => {
                                    e.target.style.borderColor = '#28AAE2';
                                    e.target.style.boxShadow = '0 0 0 4px rgba(40, 170, 226, 0.1)';
                                }}
                                onBlur={(e) => {
                                    e.target.style.borderColor = 'rgb(229, 231, 235)';
                                    e.target.style.boxShadow = '';
                                }}
                            >
                                <option>Umrah</option>
                                <option>Ramadan Umrah 2025</option>
                                <option>December Umrah</option>
                                <option>Family Umrah packages UK</option>
                                <option>Hajj packages 2025 UK</option>
                            </select>
                            <div className="absolute right-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 pointer-events-none">
                                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" d="m19.5 8.25-7.5 7.5-7.5-7.5" />
                                </svg>
                            </div>
                        </div>
                    </div>

                    {/* Travel Month */}
                    <div className="group">
                        <label className="flex items-center gap-2 text-sm font-semibold text-gray-700 mb-3">
                            <Calendar className="w-4 h-4" style={{color: '#28AAE2'}} />
                            Travel Month
                        </label>
                        <div className="relative">
                            <input 
                                type="date"
                                value={travelMonth}
                                onChange={(e) => setTravelMonth(e.target.value)}
                                className="w-full px-4 py-4 bg-white border-2 border-gray-200 rounded-2xl shadow-sm transition-all duration-300 text-gray-800 font-medium hover:shadow-md"
                                onFocus={(e) => {
                                    e.target.style.borderColor = '#28AAE2';
                                    e.target.style.boxShadow = '0 0 0 4px rgba(40, 170, 226, 0.1)';
                                }}
                                onBlur={(e) => {
                                    e.target.style.borderColor = 'rgb(229, 231, 235)';
                                    e.target.style.boxShadow = '';
                                }}
                            />
                        </div>
                    </div>

                    {/* Pilgrims */}
                    <div className="group">
                        <label className="flex items-center gap-2 text-sm font-semibold text-gray-700 mb-3">
                            <Users className="w-4 h-4" style={{color: '#28AAE2'}} />
                            Pilgrims
                        </label>
                        <div className="relative">
                            <select 
                                value={pilgrims}
                                onChange={(e) => setPilgrims(e.target.value)}
                                className="w-full px-4 py-4 bg-white border-2 border-gray-200 rounded-2xl shadow-sm transition-all duration-300 appearance-none cursor-pointer text-gray-800 font-medium hover:shadow-md"
                                onFocus={(e) => {
                                    e.target.style.borderColor = '#28AAE2';
                                    e.target.style.boxShadow = '0 0 0 4px rgba(40, 170, 226, 0.1)';
                                }}
                                onBlur={(e) => {
                                    e.target.style.borderColor = 'rgb(229, 231, 235)';
                                    e.target.style.boxShadow = '';
                                }}
                            >
                                {[...Array(10)].map((_, i) => (
                                    <option key={i} value={String(i + 1).padStart(2, '0')}>
                                        {String(i + 1).padStart(2, '0')}
                                    </option>
                                ))}
                            </select>
                            <div className="absolute right-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 pointer-events-none">
                                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" d="m19.5 8.25-7.5 7.5-7.5-7.5" />
                                </svg>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Search Button */}
                <div className="text-center">
                    <button 
                        type="button"
                        className="group inline-flex items-center gap-3 text-white px-12 py-5 rounded-2xl font-bold text-lg shadow-xl hover:shadow-2xl transform hover:scale-105 transition-all duration-300"
                        style={{background: 'linear-gradient(135deg, #28AAE2 0%, #1E90D4 100%)'}}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.background = 'linear-gradient(135deg, #1E90D4 0%, #1670B0 100%)';
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.background = 'linear-gradient(135deg, #28AAE2 0%, #1E90D4 100%)';
                        }}
                    >
                        <Search className="w-5 h-5 group-hover:rotate-12 transition-transform duration-300" />
                        Search Packages
                        <div className="w-2 h-2 bg-white rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                    </button>
                </div>

                {/* Info text */}
                <p className="text-center text-sm text-gray-500 mt-6">
                    🕌 Trusted by thousands of pilgrims • Best prices guaranteed
                </p>
            </div>
        </div>
    );
};

const Form1 = () => {
    return (
        <section className="min-h-screen flex items-center justify-center py-12 px-4 bg-gradient-to-br from-gray-50 to-gray-100">
            <div className="w-full max-w-7xl">
                <TourCard />
            </div>
        </section>
    );
};

export default Form1;