"use client"
import React, { useState, useEffect } from 'react';
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../../../lib/firebase';

interface Package {
    id: string;
    title: string;
    days: number;
    image: string;
    makkahNights: number;
    madinahNights: number;
    makkahDays: number;
    madinahDays: number;
    price: number;
    includes: {
        flights: boolean;
        transit: boolean;
        hotels: boolean;
        ziyarat: boolean;
    };
}

const UmrahPackages: React.FC = () => {
    // Dummy data
    const dummyPackages: Package[] = [
        {
            id: '1',
            title: 'Economy Umrah Package',
            days: 10,
            image: 'https://images.unsplash.com/photo-1591604129939-f1efa4d9f7fa?w=800',
            makkahNights: 5,
            madinahNights: 5,
            makkahDays: 5,
            madinahDays: 5,
            price: 1299,
            includes: {
                flights: true,
                transit: true,
                hotels: true,
                ziyarat: true,
            }
        },
        {
            id: '2',
            title: 'Standard Umrah Package',
            days: 14,
            image: 'https://images.unsplash.com/photo-1564769625905-50e93615e769?w=800',
            makkahNights: 7,
            madinahNights: 7,
            makkahDays: 7,
            madinahDays: 7,
            price: 1799,
            includes: {
                flights: true,
                transit: true,
                hotels: true,
                ziyarat: true,
            }
        },
        {
            id: '3',
            title: 'Premium Umrah Package',
            days: 21,
            image: 'https://images.unsplash.com/photo-1586724237569-f3d0c1dee8c6?w=800',
            makkahNights: 10,
            madinahNights: 11,
            makkahDays: 10,
            madinahDays: 11,
            price: 2499,
            includes: {
                flights: true,
                transit: true,
                hotels: true,
                ziyarat: true,
            }
        }
    ];

    const [packages, setPackages] = useState<Package[]>(dummyPackages);
    const [loading, setLoading] = useState<boolean>(true);

    useEffect(() => {
        const fetchPackages = async () => {
            try {
                setLoading(true);
                const packagesCollection = collection(db, 'packages');
                const packagesSnapshot = await getDocs(packagesCollection);
                
                const packagesData = packagesSnapshot.docs.map(doc => ({
                    id: doc.id,
                    ...doc.data()
                })) as Package[];
                
                // Use fetched data if available, otherwise keep dummy data
                if (packagesData.length > 0) {
                    setPackages(packagesData);
                }
            } catch (error) {
                console.error("Error fetching packages:", error);
                // Keep dummy data on error
            } finally {
                setLoading(false);
            }
        };

        fetchPackages();
    }, []);
    return (
        <section className="umrah-packages-section section-padding">
            <div className="container">
                <div className="section-title-area text-center mb-5">
                    <h2 className="section-title wow fadeInUp">
                        3 Star Umrah Packages
                    </h2>
                </div>

                {loading ? (
                    <div className="text-center py-5">
                        <div className="spinner-border text-primary" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div>
                        <p className="mt-3">Loading packages...</p>
                    </div>
                ) : packages.length > 0 ? (
                    <div className="row g-4">
                        {packages.map((pkg) => (
                            <div key={pkg.id} className="col-xl-4 col-lg-6 col-md-6">
                                <div className="package-card">
                                    <div className="package-image-wrapper">
                                        <img 
                                            src={pkg.image} 
                                            alt={pkg.title}
                                            className="package-image"
                                        />
                                        <span className="package-duration">{pkg.days} DAYS</span>
                                    </div>
                                    
                                    <div className="package-content">
                                        <h3 className="package-title">{pkg.title}</h3>
                                        
                                        <div className="package-locations">
                                            <div className="location-item">
                                                <svg className="location-icon" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                                    <path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z"></path>
                                                    <circle cx="12" cy="10" r="3"></circle>
                                                </svg>
                                                <span>Makkah {pkg.makkahNights} Nights</span>
                                            </div>
                                            <div className="location-item">
                                                <svg className="location-icon" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                                    <path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z"></path>
                                                    <circle cx="12" cy="10" r="3"></circle>
                                                </svg>
                                                <span>Madinah {pkg.madinahNights} Nights</span>
                                            </div>
                                        </div>

                                        <div className="package-includes">
                                            {pkg.includes.flights && (
                                                <div className="include-item">
                                                    <svg className="include-icon" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                        <path d="M21 16v-2l-8-5V3.5c0-.83-.67-1.5-1.5-1.5S10 2.67 10 3.5V9l-8 5v2l8-2.5V19l-2 1.5V22l3.5-1 3.5 1v-1.5L13 19v-5.5l8 2.5z"/>
                                                    </svg>
                                                    <span>Flights</span>
                                                </div>
                                            )}
                                            {pkg.includes.transit && (
                                                <div className="include-item">
                                                    <svg className="include-icon" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                        <path d="M4 16c0 .88.39 1.67 1 2.22V20c0 .55.45 1 1 1h1c.55 0 1-.45 1-1v-1h8v1c0 .55.45 1 1 1h1c.55 0 1-.45 1-1v-1.78c.61-.55 1-1.34 1-2.22V6c0-3.5-3.58-4-8-4s-8 .5-8 4v10zm3.5 1c-.83 0-1.5-.67-1.5-1.5S6.67 14 7.5 14s1.5.67 1.5 1.5S8.33 17 7.5 17zm9 0c-.83 0-1.5-.67-1.5-1.5s.67-1.5 1.5-1.5 1.5.67 1.5 1.5-.67 1.5-1.5 1.5zm1.5-6H6V6h12v5z"/>
                                                    </svg>
                                                    <span>Transit</span>
                                                </div>
                                            )}
                                            {pkg.includes.hotels && (
                                                <div className="include-item">
                                                    <svg className="include-icon" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                        <path d="M7 13c1.66 0 3-1.34 3-3S8.66 7 7 7s-3 1.34-3 3 1.34 3 3 3zm12-6h-8v7H3V5H1v15h2v-3h18v3h2v-9c0-2.21-1.79-4-4-4z"/>
                                                    </svg>
                                                    <span>Hotels</span>
                                                </div>
                                            )}
                                            {pkg.includes.ziyarat && (
                                                <div className="include-item">
                                                    <svg className="include-icon" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                                        <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
                                                    </svg>
                                                    <span>Ziyarat</span>
                                                </div>
                                            )}
                                        </div>

                                        <div className="package-footer">
                                            <div className="package-price">
                                                <span className="price-amount">£{pkg.price.toLocaleString()}</span>
                                                <span className="price-unit">/person</span>
                                            </div>
                                            <button className="view-details-btn">
                                                View Details
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                ) : (
                    <div className="text-center py-5">
                        <p>No packages available at the moment.</p>
                    </div>
                )}
            </div>

            <style jsx>{`
                .umrah-packages-section {
                    background-color: #f8f9fa;
                    padding: 80px 0;
                }

                .section-title {
                    font-size: 2.5rem;
                    font-weight: 700;
                    color: #1a1a1a;
                    margin-bottom: 2rem;
                }

                .package-card {
                    background: white;
                    border-radius: 12px;
                    overflow: hidden;
                    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
                    transition: transform 0.3s ease, box-shadow 0.3s ease;
                    height: 100%;
                }

                .package-card:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
                }

                .package-image-wrapper {
                    position: relative;
                    height: 250px;
                    overflow: hidden;
                }

                .package-image {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                .package-duration {
                    position: absolute;
                    top: 20px;
                    right: 20px;
                    background: white;
                    padding: 8px 20px;
                    border-radius: 25px;
                    font-weight: 600;
                    font-size: 0.875rem;
                    color: #1a1a1a;
                }

                .package-content {
                    padding: 25px;
                }

                .package-title {
                    font-size: 1.375rem;
                    font-weight: 700;
                    color: #1a1a1a;
                    margin-bottom: 20px;
                }

                .package-locations {
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;
                    flex-wrap: wrap;
                }

                .location-item {
                    display: flex;
                    align-items: center;
                    gap: 8px;
                    color: #666;
                    font-size: 0.9rem;
                }

                .location-icon {
                    color: #4a5568;
                }

                .package-includes {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 15px;
                    margin-bottom: 25px;
                    padding: 20px 0;
                    border-top: 1px solid #e5e7eb;
                    border-bottom: 1px solid #e5e7eb;
                }

                .include-item {
                    display: flex;
                    align-items: center;
                    gap: 6px;
                    color: #4a5568;
                    font-size: 0.9rem;
                    font-weight: 500;
                }

                .include-icon {
                    color: #2563eb;
                }

                .package-footer {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }

                .package-price {
                    display: flex;
                    align-items: baseline;
                    gap: 4px;
                }

                .price-amount {
                    font-size: 1.875rem;
                    font-weight: 700;
                    color: #1a1a1a;
                }

                .price-unit {
                    font-size: 0.9rem;
                    color: #666;
                }

                .view-details-btn {
                    background-color: #28AAE2;
                    color: white;
                    padding: 12px 28px;
                    border: none;
                    border-radius: 8px;
                    font-weight: 600;
                    font-size: 0.95rem;
                    cursor: pointer;
                    transition: background-color 0.3s ease;
                }

                .view-details-btn:hover {
                    background-color: #28AAE2;
                }

                @media (max-width: 768px) {
                    .section-title {
                        font-size: 2rem;
                    }

                    .package-title {
                        font-size: 1.25rem;
                    }

                    .price-amount {
                        font-size: 1.5rem;
                    }
                }
            `}</style>
        </section>
    );
};

export default UmrahPackages;