"use client"
import React, { useEffect, useRef } from 'react';
import loadBackgroudImages from '../Common/loadBackgroudImages';
import Slider from 'react-slick';
import parse from 'html-react-parser';
import Link from 'next/link';

const HeroBanner3 = () => {
    const sliderRef = useRef(null);

   const heroContent = [
  {
    img: "/assets/video/start.PNG", 
    video: "/assets/video/Hero1.mp4", // Add video path
    subtitle: "Book Your Umrah Today",
    title: "Your 2025 Umrah Journey from the UK Starts Here",
    content: "Flexible packages, Haram-view hotels & VIP support for a seamless spiritual experience. Book affordable Umrah packages UK with private transfers and full visa assistance.",
  },
  {
    img: "/assets/video/start.PNG",
    video: "/assets/video/Hero2.mp4", // Add video path
    subtitle: "Spiritual Journey",
    title: "Perform Umrah <br> With Comfort & Care",
    content: "From flights to hotels, we provide complete Umrah services so you can focus on your prayers and worship.",
  },
  {
    img: "/assets/video/start.PNG",
    video: "/assets/video/Hero3.mp4", // Add video path
    subtitle: "Plan Your Hajj",
    title: "Hajj Packages <br> With Trusted Guidance",
    content: "Join thousands of pilgrims in a life-changing journey, guided by experts who ensure your safety and ease.",
  },
];
    

       useEffect(() => {
         loadBackgroudImages();
         
         // Ensure all videos play independently when component mounts
         const videos = document.querySelectorAll('.hero-video');
         videos.forEach(video => {
             video.play().catch(() => {
                 console.log('Video autoplay prevented by browser');
             });
         });
     }, []);
 
     const settings = {
         dots: false,
         infinite: true,
         fade: true,
         speed: 2000,
         slidesToShow: 1,
         slidesToScroll: 1,
         arrows: false,
         autoplay: true,
         autoplaySpeed: 6000,  // Increased to 6 seconds to let videos play more
         pauseOnHover: false,  // Don't pause slider on hover
         pauseOnFocus: false,  // Don't pause slider on focus
         beforeChange: (current, next) => {
             // Keep videos playing during slide transitions
             const videos = document.querySelectorAll('.hero-video');
             videos.forEach(video => {
                 if (!video.paused) {
                     video.currentTime = 0; // Reset video to start for smoother transitions
                 }
             });
         },
         afterChange: (current) => {
             // Ensure the active slide's video is playing
             const activeSlide = document.querySelector('.slick-active .hero-video');
             if (activeSlide) {
                 activeSlide.play().catch(() => {
                     console.log('Video play failed');
                 });
             }
         },
         responsive: [
           {
             breakpoint: 1399,
             settings: {
               slidesToShow: 1,
             }
           },
           {
             breakpoint: 1199,
             settings: {
               slidesToShow: 1,
             }
           },{
             breakpoint: 575,
             settings: {
               slidesToShow: 1,
             }
           }
         ]
       };       

    return (
<section className="hero-section hero-3">
            <div className="swiper hero-slider-3">
                <div className="swiper-wrapper">
                <Slider {...settings} ref={sliderRef}>
                {heroContent.map((item, i) => (
                    <div key={i} className="swiper-slide">
                        <div className="hero-media bg-cover">
                            {/* Background Video */}
                            {item.video && (
                                <video 
                                    className="hero-video"
                                    autoPlay 
                                    muted 
                                    loop 
                                    playsInline
                                    preload="auto"
                                    onCanPlay={(e) => {
                                        e.target.play().catch(() => {
                                            // Fallback if autoplay fails
                                            console.log('Video autoplay prevented');
                                        });
                                    }}
                                >
                                    <source src={item.video} type="video/mp4" />
                                    {/* Fallback to image if video fails to load */}
                                    <img src={item.img} alt="Hero background" />
                                </video>
                            )}
                            
                            {/* Fallback background image */}
                            <div 
                                className="hero-image-fallback bg-cover" 
                                data-background={item.img}
                                style={{
                                    backgroundImage: `url(${item.img})`,
                                    display: item.video ? 'none' : 'block'
                                }}
                            ></div>
                            
                            {/* Dark overlay for better text readability */}
                            <div className="hero-overlay"></div>
                            
                            {/* Content overlaid on video */}
                            <div className="container hero-content-container">
                                    <div className="row justify-content-center">
                                        <div className="col-lg-8">
                                            <div className="hero-content">
                                                <div className="sub-title" data-animation="fadeInUp" data-delay="1.2s">
                                                {item.subtitle}
                                                </div>
                                                <h1 data-animation="fadeInUp" data-delay="1.4s">
                                                    {parse(item.title)}
                                                </h1>
                                                <p data-animation="fadeInUp" data-delay="1.6s">
                                                {item.content}
                                                </p>
                                                <div className="about-button" data-animation="fadeInUp" data-delay="1.8s">
                                                    <Link href="/tour/tour-details" className="theme-btn">View Packages<i className="bi bi-arrow-right"></i></Link>
                                                    <Link href="/tour/tour-details" className="theme-btn style-2">Plan Your Journey<i className="bi bi-arrow-right"></i></Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                            </div>
                        </div>
                     </div>
                ))}
                 </Slider>

                   </div>
                   <div className="swiper-dot">
                      <div className="dot2"></div>
                   </div>
            </div>

            {/* CSS Styles for video background and better text readability */}
            <style jsx>{`
                .hero-media {
                    position: relative;
                    height: 100vh;
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                .hero-content-container {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    z-index: 3;
                    text-align: center;
                }

                .hero-video {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    min-width: 100%;
                    min-height: 80%;
                    width: auto;
                    height: 100%;
                    transform: translate(-50%, -50%);
                    z-index: 0;
                    object-fit: cover;
                    filter: brightness(0.7) contrast(1.1);
                }

                .hero-image-fallback {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                    background-position: center;
                    background-repeat: no-repeat;
                    z-index: 0;
                    filter: brightness(0.7) contrast(1.1);
                }

                .hero-overlay {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(
                        135deg,
                        rgba(0, 0, 0, 0.3) 0%,
                        rgba(0, 0, 0, 0.2) 50%,
                        rgba(0, 0, 0, 0.3) 100%
                    );
                    z-index: 1;
                }

                .hero-content {
                    position: relative;
                    z-index: 4;
                    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.9);
                    padding: 2rem;
                    text-align: center;
                }

                .hero-content .sub-title {
                    color: #ffffff;
                    font-weight: 600;
                    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.9);
                    font-size: 1.2rem;
                    margin-bottom: 1rem;
                }

                .hero-content h1 {
                    color: #ffffff;
                    font-weight: 700;
                    text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.9);
                    font-size: 3.5rem;
                    margin-bottom: 1.5rem;
                    line-height: 1.2;
                }

                .hero-content p {
                    color: #f8f9fa;
                    font-weight: 500;
                    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.9);
                    line-height: 1.6;
                    font-size: 1.2rem;
                    margin-bottom: 2rem;
                    max-width: 600px;
                    margin-left: auto;
                    margin-right: auto;
                }

                /* Enhanced button styles for better visibility */
                .theme-btn {
                    background: rgba(255, 255, 255, 0.95) !important;
                    color: #333 !important;
                    border: 2px solid rgba(255, 255, 255, 0.3);
                    backdrop-filter: blur(10px);
                    text-shadow: none;
                    font-weight: 600;
                    transition: all 0.3s ease;
                }

                .theme-btn:hover {
                    background: rgba(255, 255, 255, 1) !important;
                    color: #000 !important;
                    transform: translateY(-2px);
                    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
                }

                .theme-btn.style-2 {
                    background: rgba(0, 0, 0, 0.8) !important;
                    color: #ffffff !important;
                    border: 2px solid rgba(255, 255, 255, 0.3);
                }

                .theme-btn.style-2:hover {
                    background: rgba(0, 0, 0, 0.95) !important;
                    color: #ffffff !important;
                }

                /* Responsive adjustments */
                @media (max-width: 768px) {
                    .hero-media {
                        height: 65vh;
                    }

                    .hero-overlay {
                        background: linear-gradient(
                            135deg,
                            rgba(0, 0, 0, 0.4) 0%,
                            rgba(0, 0, 0, 0.3) 50%,
                            rgba(0, 0, 0, 0.4) 100%
                        );
                    }

                    .hero-video {
                        filter: brightness(0.35) contrast(1.1);
                    }

                    .hero-image-fallback {
                        filter: brightness(0.35) contrast(1.1);
                    }

                    .hero-content {
                        padding: 1rem;
                    }

                    .hero-content h1 {
                        font-size: 2.5rem;
                        text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.9);
                        margin-bottom: 1rem;
                    }

                    .hero-content p {
                        font-size: 1.1rem;
                        text-shadow: 3px 3px 8px rgba(0, 0, 0, 0.9);
                        margin-bottom: 1.5rem;
                    }

                    .hero-content .sub-title {
                        font-size: 1rem;
                        margin-bottom: 0.8rem;
                    }

                    .about-button {
                        display: flex;
                        flex-direction: column;
                        gap: 1rem;
                        align-items: center;
                    }

                    .about-button .theme-btn {
                        width: 200px;
                        text-align: center;
                    }
                }

                /* Reduce motion for accessibility */
                @media (prefers-reduced-motion: reduce) {
                    .hero-video {
                        animation-play-state: paused;
                    }
                }

                /* Ensure video doesn't interfere with content on very small screens */
                @media (max-width: 480px) {
                    .hero-media {
                        height: 60vh;
                    }
                    
                    .hero-video {
                        filter: brightness(0.3) contrast(1.1);
                    }

                    .hero-image-fallback {
                        filter: brightness(0.3) contrast(1.1);
                    }
                }
            `}</style>
        </section>
    );
};

export default HeroBanner3;